// editor style vars


// basic

$bsxui-light-border-color:			#dee2e6 !default;
$bsxui-interact-border-color:		#8d96a0 !default; // @wordpress
$bsxui-default-border-style:		solid !default;
$bsxui-special-border-style:		dashed !default;
$bsxui-area-button-bg-color:		rgba(237, 239, 240, .8) !default; // @wordpress
$bsxui-placeholder-bg-color:		#eeeeee !default;


// basic composites

$bsxui-light-border:				1px $bsxui-default-border-style $bsxui-light-border-color !default;
$bsxui-interact-border:				1px $bsxui-special-border-style $bsxui-interact-border-color !default;



// isselected config panel

$bsxui-isselected-panel-bg-color: 	#ffffff !default; 
$bsxui-isselected-panel-border: 	$bsxui-light-border !default;

$bsxui-isselected-panel-mt: 		.5rem !default; 
$bsxui-isselected-panel-px: 		.25rem !default; 


